import request from '../unit/request.js'
import axios from 'axios'
const URL = process.env.VUE_APP_BASE_URL
export function selectDataApi(){
	return request({
		url:`${URL}/api/user_add`,
		method:'get'
	})
}
export function areaListApi(code){
	return request({
		url:`${URL}/api/get_area_list?area_code=${code}`,
		method:'get'
	})
}
export function uploadImgApi(data){
	return request({
		url:`${URL}//api/upload_img`,
		method:'post',
		data
	})
}
export function addApi(data){
	return request({
		url:`${URL}/api/user_add`,
		method:'post',
		data
	})
}