import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    // name: 'Home',
    // component: () => import('../views/group.vue')
	redirect:'/ceshi'
  },
  {
    path: '/success',
    name: 'Success',
    component: () => import('../views/Success.vue')
  },
  {
	  path: '/login',
	  name: 'login',
	  component: () => import('../views/login/index.vue')
  },
  {
    path: '/ceshi',
    name: 'ceshi',
    component: () => import('../views/ceshi.vue')
  },
  {
    path: '/financial',
    name: 'financial',
    component: () => import('../views/financial/index.vue')
  },
  {
    path: '/group',
    name: 'group',
    component: () => import('../views/group.vue')
  },
  
  {
    path: '/monthlyWrite',
    name: 'monthlyWrite',
    component: () => import('../views/monthlyWrite/index.vue')
  },
  {
    path: '/dailyWrite',
    name: 'dailyWrite',
    component: () => import('../views/dailyWrite/index.vue')
  },
  {
    path: '/weeklyWrite',
    name: 'weeklyWrite',
    component: () => import('../views/weeklyWrite/index.vue')
  },
  
  {
    path: '/groupCollect',
    name: 'groupCollect',
    component: () => import('../views/groupTwo.vue')
  },
  {
    path: '/database',
    name: 'database',
    component: () => import('../views/database')
  },
  {
    path: '/error',
    name: 'error',
    component: () => import('../views/error')
  }
  
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
