import axios from 'axios'
import { Toast } from 'vant'
const service = axios.create({
	timeout:5000
})

service.interceptors.response.use(
	response => {
		console.log()
		if(response.status=="200"){
			return response
		}else{
			Toast.fail('请求失败，请联系管理员');
		}
		
	}
)
export default service