<template>
	<div class="home" style="width: 100%;overflow-x: hidden;">
		<div class="title">晋环科源员工信息采集
			<!-- 	 -->
		</div>
		<van-form @submit="onSubmit">
			<div class="unTable">
				<div class="van-cell van-field van-cell--required">
					<div class="van-cell__title van-field__label">
						<span>照片</span>
					</div>
					<div style="height: 80px;position: relative;">
						<van-uploader multiple v-if="showUploadPhoto" :preview-image="false" :max-count="1" :after-read="photoEmployees"
						 :before-delete="deletePhoto" style="float: left;" />

						<div v-if="a['url']" style="position: relative;width: 80px;height: 80px;float: left;">
							<img :src="a['url']" alt="" style="width: 80px;height: 80px;" @click="yulan(a['url'])">
							<!-- <van-icon name="clear" @click="delPhoto" color="black" style="position: absolute;right: -10px;top: -10px;" size="1.5rem" /> -->
							<img src="../assets/close.png" @click="delPhoto"  style="position: absolute;right: -8px;top: -8px;width: 20px;" alt="">
						</div>
						<span style="font-size: 12px;color: rgb(200, 201, 204);display:block;width:140px;margin-left: 20px;float:left;position: absolute;left:70px;">(请上传证件照或工作照)</span>
						<br>
						<van-loading type="spinner" v-show="isLoadingPhoto" style="height: 50px;" />
					</div>
				</div>
				<van-field v-model="formData.username" ref="username" label="账号" required placeholder="该账号用于后期登录系统,最少六位" clearable />
				<van-field v-model="formData.department" label="部门" required ref="department" class="tree" readonly @click-input="showPicker('department')"
				 placeholder="请选择部门" clearable />
				<van-field v-model="formData.role" label="职务" required ref="role" class="tree" readonly @click-input="showPicker('role')"
				 placeholder="请选择职务" clearable />
				

				<van-field v-model="formData.nickname" label="姓名" ref="nickname" required maxlength="10" placeholder="请输入姓名"
				 clearable />
				<van-field v-model="formData.work_num"  label="工号" ref="work_num" maxlength="3" type="number" placeholder="请输入工号"
				 clearable />

				<van-field v-model="formData.mobile" label="手机号" ref="mobile" required placeholder="请输入手机号" type="number" clearable />
				<van-field v-model="formData.email" required label="EMail" ref="email" placeholder="请输入EMail" clearable />
				<van-field v-model="formData.id_card" label="身份证号" ref="id_card" required placeholder="请输入身份证号" clearable />
				<van-field v-model="formData.join_date" readonly required ref="join_time" label="入职时间" @click-input="showPopup('join_date')"
				 placeholder="请选择入职时间" clearable />

				<van-field v-model="formData.join_party_date" readonly label="入党时间" @click-input="showPopup('join_party_date')"
				 placeholder="请选择入党时间" clearable />
				<van-field v-model="formData.jiguan" ref="jiguan" required label="籍贯" readonly @click-input="showArea('jiguan')"
				 placeholder="请选择籍贯" clearable />
				<van-field label="民族" required ref="nation" v-model="formData.nation" readonly @click-input="showPicker('nation')"
				 placeholder="请选择民族" clearable />
				<van-field label="血型" required ref="blood_type" v-model="formData.blood_type" readonly @click-input="showPicker('blood_type')"
				 placeholder="请选择血型" clearable />
				<van-field label="政治面貌" required ref="zhenzhimianmao" v-model="formData.zhengzhimianmao" readonly @click-input="showPicker('zhengzhimianmao')"
				 placeholder="请选择政治面貌" clearable />
				<van-field label="婚姻状况" required ref="marriage" v-model="formData.marriage" readonly @click-input="showPicker('marriage')"
				 placeholder="请选择婚姻状况" clearable />
				<van-field label="家庭住址" required ref="family" v-model="formData.family" readonly @click-input="showArea('family')"
				 placeholder="请选择家庭住址" clearable />
				<van-field label="详细住址" required ref="family_address" v-model="formData.family_address_detail" maxlength="100"
				 placeholder="请输入家庭所在街道、门牌号等" clearable />
				<van-field label="现住址" required ref="now" v-model="formData.now" readonly @click-input="showArea('now')"
				 placeholder="请选择现住址" clearable />
				<van-field label="详细住址" required ref="now_address" v-model="formData.now_address_detail" maxlength="100"
				 placeholder="请输入现所在街道、门牌号等" clearable />
				<van-field label="紧急联系人" required ref="commonContactName" v-model="formData.commonContactName" placeholder="请输入紧急联系人"
				 clearable />
				<van-field label="紧急联系电话" required v-model="formData.commonContactTel" ref="commonContactTel" placeholder="请输入紧急联系人电话"
				 clearable />
			</div>
			<div class="table">
				<div class="tableTitle" style="padding: 0;"><span style="margin-left: 15px;">教育经历(从专/本科填起)</span></div>
				<div v-for="(item,index) in education_info" :key="index+'education_info'" class="list">
					<div class="van-cell van-field">
						<span class="van-cell__title van-field__label">时间</span>
						<div style="display: inline-block;text-align: left;">
							<span style="min-width: 50px;" @click="showTableChange('education_info',index,'start_date')">{{item.start_date }}
								<span v-show="!item.start_date" style="color: #C8C9CC;">选择开始时间</span></span>
							<span style="margin: 0 10px 0 15px;">至</span> <span @click="showTableChange('education_info',index,'end_date')">{{item.end_date }}
								<span v-show="!item.end_date" style="color: #C8C9CC;">选择结束时间</span></span>
						</div>
					</div>
					<van-field label="毕业院校" v-model="item.school" placeholder="请输入毕业院校" clearable />
					<van-field label="专业" placeholder="请输入专业名称" v-model="item.specialty" clearable />
					<van-field label="学历" placeholder="请选择学历" @click-input="showxueliPopHandle(index)" readonly v-model="item.education_background"
					 clearable />

					<div class="van-cell van-field">
						<span class="van-cell__title van-field__label">证书上传</span>
						<div style="display: flex;align-items: center;height: 100px;">
							<van-uploader multiple v-if="!item.uploadImgList[0]['url']" :disabled="isLoadingZhengshuEducation" :preview-image="false" :max-count="1" :before-delete="(file)=>{deletezhengshu(file,index)}"
							 :after-read="(file)=>{afterReadEducation(file,index)}" />
							<div style="position: relative;" v-if="item.uploadImgList[0]['url']" >
								<img :src="item.uploadImgList[0]['url']" style="width: 80px;height: 80px;" alt="" @click="yulan(item.uploadImgList[0]['url'])">
								<!-- <van-icon name="clear" @click="" color="black" style="position: absolute;right: -10px;top: -10px;"
								 size="1.5rem" /> -->
								 <img src="../assets/close.png" @click="delZhengshuEducation(index)"  style="position: absolute;right: -8px;top: -8px;width: 20px;" alt="">
							</div>
							<van-loading type="spinner" v-show="isLoadingZhengshuEducation" />
						</div>
					</div>

					<div style="display: flex;height: 40px;">
						<div style="flex: 1; border-right: 1px solid #ebedf0;height: 100%;" @click="addTable('education_info',index)">
							<van-icon name="add-o" size="1.5rem" color="#646566" style="line-height: 40px;" />
						</div>
						<div style="flex:1;height: 100%;line-height: 40px;" @click="delTab(index,'education_info')">
							<van-icon name="delete" size="1.5rem" color="#646566" style="line-height: 40px;" />
						</div>
					</div>
				</div>
			</div>
			<div class="table">
				<div class="tableTitle">工作经历(当前单位不填)</div>
				<div v-for="(item,index) in job_history" :key="index+'job_history'" class="list">
					<div class="van-cell van-field">
						<span class="van-cell__title van-field__label">时间</span>
						<div style="display: inline-block;text-align: left;">
							<span style="min-width: 50px;" @click="showTableChange('job_history',index,'start_date')">{{item.start_date }}
								<span v-show="!item.start_date" style="color: #C8C9CC;">选择开始时间</span> </span>
							<span style="margin: 0 10px 0 15px;">至</span> <span @click="showTableChange('job_history',index,'end_date')">{{item.end_date}}<span
								 v-show="!item.end_date" style="color: #C8C9CC;">选择结束时间</span></span>
						</div>
					</div>
					<van-field label="单位名称" v-model="item.company" placeholder="请输入单位名称" clearable />
					<van-field label="职务" v-model="item.post" placeholder="请输入职务" clearable />
					<van-field rows="4" autosize label="工作描述" v-model="item.description" type="textarea" :maxlength="200" placeholder="请输入工作描述(200字以内)" />
					<div style="display: flex;height: 40px;">
						<div style="flex: 1; border-right: 1px solid #ebedf0;height: 100%;line-height: 40px;" @click="addTable('job_history',index)">
							<van-icon name="add-o" size="1.5rem" color="#646566" style="line-height: 40px;" />
						</div>
						<div style="flex:1;height: 100%;line-height: 40px;" @click="delTab(index,'job_history')">
							<van-icon name="delete" size="1.5rem" color="#646566" style="line-height: 40px;" />
						</div>
					</div>
				</div>
			</div>
			<div class="table">
				<div class="tableTitle">个人证书</div>
				<div v-for="(item,index) in credentials_info" :key="index+'credentials_info'" class="list">
					<van-field label="证书名称" v-model="item.title" placeholder="请输入证书名称" clearable />
					<van-field label="证书编号" v-model="item.certificate_number" placeholder="请输入证书编号" clearable />
					<div class="van-cell van-field">
						<span class="van-cell__title van-field__label">获取时间</span>
						<div style="display: inline-block;text-align: left;" @click="showPopup('credentials_info',index)">
							<span>{{item.get_date || ''}} <span style="color: #C8C9CC;" v-show="!item.get_date">选择获取时间</span> </span>
						</div>
					</div>
					<div class="van-cell van-field">
						<span class="van-cell__title van-field__label">证书上传</span>
						<div style="display: flex;align-items: center;height: 100px;">
							<van-uploader multiple v-if="!item.uploadImgList[0]['url']" :disabled="isLoadingZhengshu" :preview-image="false" :max-count="1" :before-delete="(file)=>{deletezhengshu(file,index)}"
							 :after-read="(file)=>{afterRead(file,index)}" />
							<div style="position: relative;" v-if="item.uploadImgList[0]['url']">
								<img :src="item.uploadImgList[0]['url']" style="width: 80px;height: 80px;" alt="" @click="yulan(item.uploadImgList[0]['url'])">
								<!-- <van-icon name="clear" @click="(index)" color="black" style="position: absolute;right: -10px;top: -10px;"
								 size="1.5rem" /> -->
								 <img src="../assets/close.png" @click="delZhengshu(index)"  style="position: absolute;right: -8px;top: -8px;width: 20px;" alt="">
								 
							</div>
							<van-loading type="spinner" v-show="isLoadingZhengshu" />
						</div>
					</div>
					<div style="display: flex;height: 40px;">
						<div style="flex: 1; border-right: 1px solid #ebedf0;height: 100%;line-height: 40px;" @click="addTable('credentials_info',index)">
							<van-icon name="add-o" size="1.5rem" color="#646566" style="line-height: 40px;" />
						</div>
						<div style="flex:1;height: 100%;line-height: 40px;" @click="delTab(index,'credentials_info')">
							<van-icon name="delete" size="1.5rem" color="#646566" style="line-height: 40px;" />
						</div>
					</div>
				</div>
			</div>
			<div style="height: 50px;"></div>
			<div class="fixed">
				<van-button style="width: 100%;" type="info" @click="submit">
					提交
				</van-button>
			</div>
		</van-form>
		<!-- 时间选择器 -->
		<van-popup v-model="show" position="bottom" @close="currentDate= new Date(1970,0,1)">
			<van-datetime-picker @confirm="choseData" v-model="currentDate" ref="dataChose" cancel-button-text="清空" @cancel="clearData"
			 type="date" title="选择年月日" :min-date="minDateFull" :max-date="maxDateFull" />
		</van-popup>
		<!-- 年月选择器 -->
		<van-popup v-model="showTable" position="bottom" @close="currentDate= new Date(1970,0)">
			<van-datetime-picker @confirm="choseTableData" v-model="currentShort" ref="nianyueChose" cancel-button-text="清空"
			 @cancel="clearTableData" type="year-month" title="选择年月" :min-date="minDate" :max-date="maxDate" />
		</van-popup>
		<!-- 地址三级联动选择 -->
		<van-popup v-model="showAreaPopup" position="bottom" @close="closeArea">
			<van-area title="省市区选择" @confirm='choseArea' :value="areaDefault" ref="areaDialog" cancel-button-text="清空" @cancel="clearArea"
			 :loading="areaLoading" @change="changeArea" :area-list="areaList" />
		</van-popup>
		<div v-show="loading">
			<van-loading color="#1989fa" style="position: fixed; top: 50%;left: 50%;transform: translate(-50%,-50%);z-index: 10000;" />
			<van-overlay :show="true" @click="show = false" />
		</div>
		<!-- 树形弹框 -->
		<van-popup v-model="showPickerBoolean" round position="bottom" :class="{tree:treeShow}">
			<van-picker show-toolbar :columns="columns" :defaultIndex="defaultPickerNum" @confirm="changePickerData" @cancel="clearPickerBoolean"
			 cancel-button-text="清空" />
		</van-popup>
		<van-popup v-model="showxueliPop" round position="bottom">
			<van-picker show-toolbar :columns="xueliList" @confirm="changexueli" @cancel="clearInput" cancel-button-text="清空选中内容" />
		</van-popup>


	</div>
</template>

<script>
	import { ImagePreview } from 'vant';
	import {
		selectDataApi,
		areaListApi,
		uploadImgApi,
		addApi
	} from '../api/employees.js'
	import {
		marriageList,
		xueliList,
		zhengzhimianmao,
		nation,
		xuexing
	} from '../data/selectData.js'
	export default {
		data() {
			var a = window.localStorage.getItem('jhkyEmployeesMessage')
			if (a) {
				var b = JSON.parse(a)
			} else {
				var b = {
					a: {
						url: ''
					}
				}
			}
			return {
				showxueliPop: false,
				showPickerBoolean: false,
				pickType: '',
				defaultPickerNum: '',
				columns: ['ces'],
				addressType: '',
				dataType: "",
				loading: true,
				areaLoading: true,
				tableTimeType: '',
				a: b.a,
				b: '未婚',
				jiguan: [],
				show: false,
				showTable: false,
				areaDefault: '',
				showAreaPopup: false,
				marriageList: [],
				xueliList: [],
				nation: [],
				zhengzhimianmao,
				formData: {
					username: '',
					photo_id: '',
					// password: '',
					role_id: '',
					role: '',
					department_id: '', //部门
					department: '',
					nickname: '', //姓名
					mobile: '', //手机号
					surePwd: '', //密码
					work_num: "", //工号
					email: '', //详细地址
					emailLater: '',
					id_card: '', //法人姓名
					sex: 1, //法人联系方式
					age: '',
					join_party_date: '',
					jiguan: '',
					province_code: '',
					city_code: '',
					truename: '',
					district_code: '',
					now_province_code: '',
					now_city_code: '',
					now_district_code: '',
					family: '',
					family_province_code: '',
					family_city_code: '',
					family_district_code: '',
					family_address_detail: '',
					blood_type: '',
					nation: '', //民族
					zhengzhimianmao: '', //政治面貌
					xianzhuzhi: '',
					now: '',
					now_address_detail: '', //现住址详细地址
					commonContactName: '', //常用联系人姓名
					commonContactTel: '', //常用联系人联系方
					mark: '', //备注,
					join_date: '',
					leave_date: '',
					marriage: ''
				},
				education_info: [{
					start_date: '', //开始时间
					end_date: '', //结束时间

					school: '', //学校
					specialty: '', //专业
					education_background: '', //学历
					education_img_id: '',
					uploadImgList: [{
						url: ''
					}]
				}],
				job_history: [{
					time: [],
					start_date: '', //开始时间
					end_date: '', //结束时间
					description: '', //描述
					company: '',
					post: ''
				}],
				credentials_info: [{
					title: '', //证书名称
					certificate_number: '', //证书编号	
					get_date: '', //获取时间
					certificate_img_id: '', //证书图片id
					uploadImgList: [{
						url: ''
					}]
				}],
				areaList: {
					province_code: {},
					city_list: {},
					county_list: {}
				},
				isLoadingPhoto: false,
				selectDataUrl: {},
				credentials_infoIndex: '',
				minDateFull: new Date(1970, 0, 1),
				maxDateFull: new Date(),
				minDate: new Date(1970, 0, ),
				maxDate: new Date(),
				currentDate: new Date(1970, 0, 1),
				currentShort: new Date(1970, 0),
				xuexing,
				treeShow: false,
				b: [],
				isLoadingZhengshu: false,
				isLoadingZhengshuEducation: false,
				showUploadPhoto: true,
				showUploadZhengshu: true
			}
		},
		mounted() {
			document.title = '晋环科源员工信息采集'
			const that = this
			var a = window.localStorage.getItem('jhkyEmployeesMessage')
			if (a) {
				var b = JSON.parse(a)
				this.b = b
				this.formData.photo_id = b.photo_id
				this.formData.username = b.username
				this.formData.role_id = b.role_id
				this.formData.department_id = b.department_id
				this.formData.nickname = b.truename
				this.formData.work_num = b.work_num
				this.formData.id_card = b.id_card
				this.formData.nation = b.nation
				this.formData.zhengzhimianmao = b.political_status
				this.education_info = b.education_info
				this.credentials_info = b.credentials_info
				this.job_history = b.job_history
				this.formData.mobile = b.mobile
				this.formData.email = b.email
				this.formData.commonContactName = b.emergency_contact[0].truename
				this.formData.commonContactTel = b.emergency_contact[0].mobile
				this.formData.now_address_detail = b.now_address_detail
				this.formData.province_code = b.province_code
				this.formData.city_code = b.city_code
				this.formData.district_code = b.district_code
				this.formData.now_province_code = b.now_province_code
				this.formData.now_city_code = b.now_city_code
				this.formData.now_district_code = b.now_district_code
				this.formData.family_province_code = b.family_province_code
				this.formData.family_city_code = b.family_city_code
				this.formData.family_district_code = b.family_district_code
				this.formData.join_date = b.join_date
				this.formData.blood_type = b.blood_type
				this.formData.family_address_detail = b.family_address_detail
				this.formData.marriage = b.marriage
				this.formData.role = b.role
				this.formData.department = b.department
				this.formData.jiguan = b.jiguan
				this.formData.family = b.family
				this.formData.now = b.now
				this.a = b.a
				this.formData.join_party_date = b.join_party_date
			}
			if (this.education_info == '') {
				this.education_info = [{
					start_date: '', //开始时间
					end_date: '', //结束时间
					time: [],
					school: '', //学校
					specialty: '', //专业
					education_background: '', //学历
					education_img_id: '',
					uploadImgList: [{
						url: ''
					}]
				}]
			}
			if (this.job_history == '') {
				this.job_history = [{
					time: [],
					start_date: '', //开始时间
					end_date: '', //结束时间
					description: '', //描述
					company: '',
					post: ''
				}]
			}
			if (this.credentials_info == '') {
				this.credentials_info = [{
					title: '', //证书名称
					certificate_number: '', //证书编号	
					get_date: '', //获取时间
					certificate_img_id: '', //证书图片id
					uploadImgList: [{
						url: ''
					}]
				}]
			}
			if (this['a']['url']) {
				this['showUploadPhoto'] = false
			}
			// console.log(b.education_info, 'b')
			this.changeDataType()
			this.getSelectData()
			window.addEventListener('pagehide', e => {
				// console.log(that.credentials_info[0].)
				var data = that.formData
				var formData = {
					photo_id: data.photo_id,
					username: data.username,
					role_id: data.role_id,
					department_id: data.department_id,
					// password: data.password,
					truename: data.nickname,
					work_num: data.work_num,
					id_card: data.id_card,
					nation: data.nation,
					political_status: data.zhengzhimianmao,
					education_info: that.education_info,
					credentials_info: that.credentials_info,
					job_history: that.job_history,
					mobile: data.mobile,
					remark: '',
					email: data.email,
					emergency_contact: [{
						truename: data.commonContactName,
						mobile: data.commonContactTel
					}],
