			choseData(value) {
				var a = new Date(value)
				var month, day, data;
				parseInt(a.getMonth() + 1) < 10 ? month = '0' + (a.getMonth() + 1) : month = parseInt(a.getMonth() + 1)
				parseInt(a.getDate()) < 10 ? day = '0' + a.getDate() : day = parseInt(a.getDate())
				data = a.getFullYear() + '-' + month + '-' + day
				if (this.dataType == 'credentials_info') {
					this.$set(this.credentials_info[this.credentials_infoIndex], 'get_date', data)
				} else {
					this.formData[this.dataType] = data
