					name
				} = this.tableTimeType
				var a = new Date(value),
					month
				parseInt(a.getMonth() + 1) < 10 ? month = '0' + (a.getMonth() + 1) : month = (a.getMonth() + 1)
				var data = a.getFullYear() + '-' + month
				if (name == 'start_date' && this[type][index]['end_date']) {
					var end_date = new Date(this[type][index]['end_date']).getTime()
					var start_date = new Date(data).getTime()
					if (start_date < end_date) {
						this[type][index][name] = data
						this.currentShort = new Date(1970, 0)
						this.showTable = false
					} else {
						this.$toast('开始时间不能在结束时间之后')
					}
				} else if (name == 'end_date' && this[type][index]['start_date']) {
					var end_date = new Date(data).getTime()
					var start_date = new Date(this[type][index]['start_date']).getTime()
					if (start_date < end_date) {
						this[type][index][name] = data
						this.currentShort = new Date(1970, 0)
						this.showTable = false
					} else {
						this.$toast('结束时间不能在开始时间之前')
					}
				} else {
