			choseArea(arr) {

				var s = '',
					sn = '',
					c = '',
					cn = '',
					x = '',
					xn = ''
				if (arr[0]) {
					s = arr[0].code
					sn = arr[0].name
					sn == '请选择' ? sn = s = '' : sn = sn
				}
				if (arr[1]) {
					c = arr[1].code
					cn = arr[1].name
					cn == '请选择' ? cn = c = '' : cn = cn
				}
				if (arr[2]) {
					x = arr[2].code
					xn = arr[2].name
					xn == '请选择' ? xn = x = '' : xn = xn
				}
				if (this.addressType == 'now') {
					this.formData.now_province_code = s
					this.formData.now_city_code = c
					this.formData.now_district_code = x
					this.formData.now = sn + cn + xn + ''
				}
				if (this.addressType == 'jiguan') {
					this.formData.province_code = s
					this.formData.city_code = c
					this.formData.district_code = x
					this.formData.jiguan = sn + cn + xn + ''
				}
				if (this.addressType == 'family') {
					this.formData.family_province_code = s
					this.formData.family_city_code = c
					this.formData.family_district_code = x
					this.formData.family = sn + cn + xn + ''
				}
				this.$refs.areaDialog.reset("000000000000")
				this.areaDefault = ''
				this.showAreaPopup = false

			},
			clearArea() {
				if (this.addressType == 'now') {
					this.formData.now_province_code = ''
					this.formData.now_city_code = ''
					this.formData.now_district_code = ''
					this.formData.now = ''
				}
				if (this.addressType == 'jiguan') {
					this.formData.province_code = ''
					this.formData.city_code = ''
					this.formData.district_code = ''
					this.formData.jiguan = ''
				}
				if (this.addressType == 'family') {
					this.formData.family_province_code = ''
					this.formData.family_city_code = ''
					this.formData.family_district_code = ''
					this.formData.family = ''
				}
				this.$refs.areaDialog.reset("000000000000")
				this.areaDefault = ''
				this.showAreaPopup = false
			},
			closeArea() {
				this.$refs.areaDialog.reset("000000000000")
				this.areaDefault = ''
			},
			delPhoto() {
				this.a['url'] = ''
				this.formData.photo_id = ''
				this.showUploadPhoto = true
			},
			checkIdCard(value) {
				var regIdCard =
					/^(^[1-9]\d{7}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}$)|(^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])((\d{4})|\d{3}[Xx])$)$/;
				if (regIdCard.test(value)) {
					if (value.length == 18) {
						var idCardWi = new Array(7, 9, 10, 5, 8, 4, 2, 1, 6, 3, 7, 9, 10, 5, 8, 4, 2); //将前17位加权因子保存在数组里
						var idCardY = new Array(1, 0, 10, 9, 8, 7, 6, 5, 4, 3, 2); //这是除以11后，可能产生的11位余数、验证码，也保存成数组
						var idCardWiSum = 0;
						for (var i = 0; i < 17; i++) {
							idCardWiSum += value.substring(i, i + 1) * idCardWi[i];
						}
						var idCardMod = idCardWiSum % 11;
						var idCardLast = value.substring(17);
						if (idCardMod == 2) {
							if (idCardLast == "X" || idCardLast == "x") {
								return true;
							} else {
								return false;
							}
						} else {
							if (idCardLast == idCardY[idCardMod]) {
								return true
							} else {
								return false
							}
						}
					} else {
						return false
					}
				} else {
					return false
				}
			},
			changeRole() {
				let roleList = []
				let roleRecursive = (data) => {
					for (let i = 0; i < data.length; i++) {
						if (data[i].level == 2) {
							roleList.push({
								text: '' + data[i].name,
								value: data[i].id
							})
						} else if (data[i].level == 3) {
							roleList.push({
								text: '|——' + data[i].name,
								value: data[i].id
							})
						} else if (data[i].level == 4) {
							roleList.push({
								text: '|————' + data[i].name,
								value: data[i].id
							})
						} else if (data[i].level == 5) {
							roleList.push({
								text: '|——————' + data[i].name,
								value: data[i].id
							})
						} else if (data[i].level == 6) {
							roleList.push({
								text: '|————————' + data[i].name,
								value: data[i].id
							})
						} else {
							roleList.push({
								text: data[i].name,
								value: data[i].id
							})
						}
						if (data[i].child) {
							roleRecursive(data[i].child)
						}
					}
				}
				roleRecursive(this.selectDataUrl.role_list)
				this.selectDataUrl.role_list = roleList
				this.selectDataUrl.role_list.shift()
				this.selectDataUrl.role_list.shift()
			},
			changeDepartment() {
				let department = []
				let departmentRecursive = (data) => {
					for (let i = 0; i < data.length; i++) {
						if (data[i].level == 2) {
							department.push({
								text: '' + data[i].name,
								value: data[i].id
							})
						} else if (data[i].level == 3) {
							department.push({
								text: '|——' + data[i].name,
								value: data[i].id
							})
						} else if (data[i].level == 4) {
							department.push({
								text: '|————' + data[i].name,
								value: data[i].id
							})
						} else {
							department.push({
								text: data[i].name,
								value: data[i].id
							})
						}

						if (data[i].child) {
							departmentRecursive(data[i].child)
						}
					}
				}
				departmentRecursive(this.selectDataUrl.department_list)
				this.selectDataUrl.department_list = department
				this.selectDataUrl.department_list.shift()
			},
			yulan(url) {
				ImagePreview({
					images: [
						url
					],
					closeable: true,
				});
			},
			checkPhone(value) {
				if (value != '') {
					const reg = /^1[3|4|5|6|7|8|9][0-9]\d{8}$/
					if (reg.test(value)) {
						return true
					} else {
						return false
					}
				} else {
					return true
				}
			},
			changexueli(value) {
				this.education_info[this.xueliIndex]['education_background'] = value.value
				this.showxueliPop = false
			},
			clearInput() {
				this.education_info[this.xueliIndex]['education_background'] = ''
				this.showxueliPop = false
			},
			checkEmail(value) {
				if (value != '') {
					const reg = /^\w+((.\w+)|(-\w+))@[A-Za-z0-9]+((.|-)[A-Za-z0-9]+).[A-Za-z0-9]+$/;
					if (reg.test(value)) {
						return true
					} else {
						return false
					}
				} else {
					return true
				}
			},
			addTable(name, index) {
				switch (name) {
					case 'education_info':
						var data = {
							start_date: '', //开始时间
							end_date: '', //结束时间
							time: [],
							school: '', //学校
							specialty: '', //专业
							education_background: '', //学历
							education_img_id: '',
							uploadImgList: [{
								url: ''
							}]
						}
						this[name].splice(index + 1, 0, data)
						break;
					case 'job_history':
						var data = {
							time: [],
							start_date: '', //开始时间
							end_date: '', //结束时间
							description: '',
							company: '',
							post: ''
						}
						this[name].splice(index + 1, 0, data)
						break;
					case 'credentials_info':
						var data = {
							title: '', //证书名称
							certificate_number: '', //证书编号	
							get_date: '', //获取时间
							certificate_img_id: '', //证书图片id
							uploadImgList: [{
								url: ''
							}]
						}
						this[name].splice(index + 1, 0, data)
				}
			},
			delTab(index, name) {
				if (this[name].length > 1) {
					this.$dialog.confirm({
							title: '删除',
							message: '您确认要删除这条内容吗',
						})
						.then(() => {
							this[name].splice(index, 1)
						})
						.catch(() => {
							// on cancel
						});

				} else {
					this.$toast('当前已经是最后一条')
				}
			},
			checkUsername(value) {
				if (value != '') {
					if (value.length < 6) {
						return false
					}
				} else {
					return true
				}
			},
			async afterRead(file, index) {
				this.showUploadZhengshu = false
				var data = new FormData()
				data.append('img', file.file)
				this.isLoadingZhengshu = true
				var result = await uploadImgApi(data)
				if (result.data.code == "00000") {
					this.isLoadingZhengshu = false
					this.credentials_info[index]['certificate_img_id'] = result.data.data.id
					this.credentials_info[index]['uploadImgList'][0]['url'] = result.data.data.url

				} else {
					this.$toast(result.data.msg)
					this.showUploadZhengshu = true
					this.isLoadingZhengshu = false
				}
			},
			deletezhengshu(file, index) {
				this.credentials_info[index]['certificate_img_id'] = ''
				this.credentials_info[index]['uploadImgList'][0] = {
					url: ''
				}
				this.showUploadZhengshu = true
				// return true
			},
			async afterReadEducation(file, index) {
				this.showUploadZhengshuEducation = false
				var data = new FormData()
				data.append('img', file.file)
				this.isLoadingZhengshuEducation = true
				var result = await uploadImgApi(data)
				if (result.data.code == "00000") {
					this.isLoadingZhengshuEducation = false
					this.education_info[index]['education_img_id'] = result.data.data.id
					this.education_info[index]['uploadImgList'][0]['url'] = result.data.data.url
				} else {
					this.$toast(result.data.msg)
					this.showUploadZhengshuEducation = true
					this.isLoadingZhengshuEducation = false
				}
			},
			async photoEmployees(file) {
				this.showUploadPhoto = false
				var data = new FormData()
				data.append('img', file.file)
				this.isLoadingPhoto = true
				var result = await uploadImgApi(data)
				if (result.data.code == "00000") {
					this.isLoadingPhoto = false
					this.formData.photo_id = result.data.data.id
					this.a = {
						url: result.data.data.url
					}
				} else {
					this.$toast(result.data.msg)
					this.showUploadPhoto = true
					this.isLoadingPhoto = false
				}
			},
			deletePhoto() {
				this.formData.photo_id = ''
				this.a = []
				this.showUploadPhoto = true
			}
		}
	}
</script>

